<template>
    <views>
        <nav-bar
            :title="moduleTitle"
            fixed
            @click-left="onClickLeft">
        </nav-bar>
        <loading
            v-if="loadingData"
            color="#1989fa">
        </loading>
        <views
            v-else
            class="detail">
            <views class="detail-name">{{ detail.name }}</views>
            <views class="detail-time">{{ detail.created_at }}</views>
            <views class="collect" @click="collectArticle">
                <images :src="collectIcon"></images>
            </views>
            <views
                v-html="detail.detail_body"
                class="detail-content">
            </views>
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import Loading from '@/components/loading'
import api from '@/utils/api'
import tools from '@/mixins/tools'
export default {
    mixins: [tools],
    components: {
        Loading,
        NavBar
    },
    data() {
        return {
            detail: {},
            loadingData: false,
            collectIcon: require('@/assets/images/collect.png')
        }
    },
    computed: {
        query() {
            return this.$route.query
        },
        moduleTitle() {
            return this.query.name
        }
    },
    watch: {
        'query.id': {
            handler(n, o) {
                if (n) this.getArticleDetail(n)
            },
            immediate: true
        }
    },
    methods: {
        onClickLeft() {
            this.back()
        },
        collectArticle() {
            api.collectArticle().then(() => {
                this.sucMsg('收藏成功')
            })
        },
        getArticleDetail(id) {
            this.loadingData = true
            api.getArticleDetail({ id }).then((res) => {
                this.detail = res.data
                this.loadingData = false
            }).catch(() => {
                this.loadingData = false
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
