<template>
    <van-nav-bar
        :title="title"
        :border="false"
        :fixed="fixed"
        :left-arrow="leftArrow"
        :class="[{'white': theme === 'white'}, {'absolute': absolute}]"
        @click-left="onClickLeft">
        <template #left>
            <views v-if="leftArrow" class="icon">
                <images :src="theme === 'white' ? leftWhiteIcon : leftIcon"></images>
            </views>
        </template>
    </van-nav-bar>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            default: ''
        },
        leftArrow: {
            type: Boolean,
            default: true
        },
        fixed: {
            type: Boolean,
            default: false
        },
        absolute: {
            type: Boolean,
            default: false
        },
        theme: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            leftIcon: require('@/assets/images/arrow_left.png'),
            leftWhiteIcon: require('@/assets/images/arrow_left_white.png')
        }
    },
    methods: {
        onClickLeft() {
            this.$emit('click-left')
        }
    }
}
</script>

<style lang="scss">
@import './index.scss';
</style>
